import React, { FC, useState } from "react";

const ThemeSwitcher: FC<{}> = (): JSX.Element => {
  const [theme, setTheme] = useState("");
  const themeClass = theme ? theme.toLowerCase() : "secondary";

  const chooseTheme = (
    theme: string,
    event: React.MouseEvent<HTMLButtonElement>
  ): void => {
    event.preventDefault();
    setTheme(theme);
  };

  return (
    <li className="nav-item dropdown">
      <a
        className="nav-link dropdown-toggle"
        href="/"
        id="dropdown01"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        Theme
      </a>
      <ul className="dropdown-menu" aria-labelledby="dropdown01">
        <li>
          <span className={`dropdown-item text-${themeClass}`}>
            {theme || "Default"}
          </span>
        </li>
        <li>
          <button
            className="dropdown-item"
            onClick={(e) => chooseTheme("Primary", e)}
          >
            Primary Theme
          </button>
        </li>
        <li>
          <button
            className="dropdown-item"
            onClick={(e) => chooseTheme("Danger", e)}
          >
            Danger Theme
          </button>
        </li>
        <li>
          <button
            className="dropdown-item"
            onClick={(e) => chooseTheme("Success", e)}
          >
            Success Theme
          </button>
        </li>
        <li>
          <button className="dropdown-item" onClick={(e) => chooseTheme("", e)}>
            Default Theme
          </button>
        </li>
      </ul>
    </li>
  );
};

export default ThemeSwitcher;
